.app {
  width: 100%;
  min-height: 100vh;
  background-color: #2F2F2F;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 3rem 2rem;
  font-family: 'Courier New', 'Monaco', 'Menlo', monospace;
}

.container {
  max-width: 900px;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.main-header {
  text-align: center;
  margin-bottom: 0.5rem;
  padding-bottom: 2rem;
}

.main-header h1 {
  color: #ffffff;
  font-size: 2.5rem;
  font-weight: 600;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  margin: 0;
  text-shadow: 0 2px 8px rgba(0, 0, 0, 0.5);
  font-family: 'Medodica', 'Cinzel', serif;
}

.section {
  position: relative;
  background: rgba(0, 0, 0, 0.4);
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.section:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.4);
}

/* Colored vertical bars */
.section::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 6px;
  z-index: 1;
}

.green-bar::before {
  background: linear-gradient(180deg, #00D4FF 0%, #00A8CC 100%);
}

.pink-bar::before {
  background: linear-gradient(180deg, #00FF88 0%, #00CC6A 100%);
}

.yellow-bar::before {
  background: linear-gradient(180deg, #C77DFF 0%, #9D4EDD 100%);
}

.section-content {
  padding: 2rem 2.5rem 2rem 3rem;
  color: #ffffff;
  line-height: 1.7;
}

.section-header {
  padding: 1.25rem 2.5rem 1.25rem 3rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.port-label {
  font-weight: bold;
  font-size: 1.1rem;
  color: #ffffff;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}

.section-text {
  margin: 0;
  color: #e5e5e5;
  font-size: 1rem;
  line-height: 1.8;
}

.section-text strong {
  color: #ffffff;
  font-weight: 600;
}

.definition-list {
  margin-top: 1.5rem;
  padding-left: 1.5rem;
  border-left: 2px solid rgba(255, 255, 255, 0.1);
}

.definition-item {
  margin-bottom: 1.25rem;
  color: #d1d1d1;
  font-size: 0.95rem;
  line-height: 1.75;
}

.definition-item:last-child {
  margin-bottom: 0;
}

.definition-item strong {
  color: #ffffff;
  font-weight: 600;
}

@media (max-width: 768px) {
  .app {
    padding: 2rem 1.5rem;
  }

  .container {
    gap: 1.25rem;
  }

  .main-header {
    padding-bottom: 1.5rem;
  }

  .main-header h1 {
    font-size: 2rem;
    letter-spacing: 0.08em;
  }

  .section-content {
    padding: 1.5rem 2rem 1.5rem 2.5rem;
  }

  .section-header {
    padding: 1rem 2rem 1rem 2.5rem;
  }

  .section-text {
    font-size: 0.95rem;
  }

  .definition-item {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .app {
    padding: 1.5rem 1rem;
  }

  .container {
    gap: 1rem;
  }

  .main-header {
    padding-bottom: 1rem;
  }

  .main-header h1 {
    font-size: 1.5rem;
    letter-spacing: 0.05em;
  }

  .section-content {
    padding: 1.25rem 1.5rem 1.25rem 2rem;
  }

  .section-header {
    padding: 0.875rem 1.5rem 0.875rem 2rem;
  }

  .port-label {
    font-size: 1rem;
  }

  .section-text {
    font-size: 0.9rem;
    line-height: 1.6;
  }

  .definition-item {
    font-size: 0.85rem;
    line-height: 1.6;
  }

  .definition-list {
    padding-left: 1rem;
  }
}

